.leader-card-container{

    display: flex;
    flex-wrap: wrap;
    align-items: stretch;

}

.leader-card{
    background-color: $white;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(151, 151, 151, 1);
    -moz-box-shadow:    1px 1px 5px 0px rgba(151, 151, 151, 1);
    box-shadow:         1px 1px 5px 0px rgba(151, 151, 151, 1);
    padding: 20px 20px 50px;
    text-align: center;
    margin-bottom: 20px;
    @include respond-to('md'){
        margin-bottom: 100px;
        display: inline-block;
    }
    &__img img{
        width: 100%;
        min-height: 336px;
    }
    &__category{
        @include _bodyCopy;
        font-size: 14px;
        text-transform: uppercase;
        padding-bottom: 25px;
    }
    &__name{
        @include _bodyCopy;
        font-size: 26px;
        padding-bottom: 10px;
        line-height: 35px;
        padding-top: 55px;
    }
    p{
        font-size: 16px;
        line-height: 24px;
    }
    &:last-child{
        margin-bottom: 60px;
        @include respond-to('md'){
            margin-bottom: 20px;
        }
    }
} 
