#searchform{
	@include respond-to('md'){
		padding: 0 0 78px 0;
	}
    .form-control{
        border: none;
        border-bottom: thin solid #ccc;
        padding-left: 20px;
        color: #666;
        
        &::placeholder{
            color: #ccc;
            text-transform: uppercase;
        }
    }
    span.before{
        color: #00A9D8;
        font-size: 25px;
        position: relative;
        top: 6px;
        left: 9px;
        z-index: 10;
    }
    #searchsubmit{
        background-image: url(../assets/img/search.svg);
        background-repeat: no-repeat;
        background-size: cover;
        padding: 0;
        width: 35px;
        height: 35px;
        border: none;
        background-position: center;
        background-color: transparent;
        margin-left: 20px;
    }
    
    #close-search {
	    display: none;
	    
	    &:hover {
		   cursor: pointer; 
	    }
	   @include respond-to('md'){
		   display: block;
		   position: relative;
		   top: 8px;
		   left: -15px;
		   color: #00A9D8;
	   }
    }
}