.generic{
    background-color: #F1F1F1;
    padding: 55px 0;
    text-align: center;
    @include respond-to('md'){
        padding: 75px 0;
    }
    @include respond-to('lg'){
        padding: 100px 0;
    }    
    .strapline{
        margin-bottom: 43px;
    }
    h1, .h1{
        margin-bottom: 30px;
    }
    h2, .h2{
        margin-bottom: 2rem;
    }
}

.content {
	width: 100%;
	margin: 0 auto;
	@include respond-to('md'){
		width: 90%;
	}
}

.grey-w-icon{
    background-image: url(../assets/img/wo-logo-grey.svg);
    background-size: cover;
    position: relative;
    background-position: center;
    padding: 70px 0;
    @include respond-to('md'){
        padding: 80px 0;
    }
    @include respond-to('lg'){
        padding: 90px 0;
    }
}

.subsection{
    h3, .h3{
        margin-bottom: 20px;
    }
    h4, .h4{
        text-transform: uppercase;
        margin-bottom: 24px;
        font-size: 15px;
    }
    p{
        margin-bottom: 15px;
    }
}

.three-col{
    column-count: 1;
    @include respond-to('md'){
        column-count: 3;
    }
}

.block-centre{
    // margin: 1rem auto; Only appears to be used for About template - GM - CHECK

    margin: 0 auto;

}

.bk-grey{
    background-color: #F1F1F1;  
}

.bk-white{
    background-color: #ffffff;  
}

.overlay {
	filter: opacity(.5);
}

.container-fluid {
	overflow: hidden;
	padding: 0;
}

.wrapper {
	padding: 0;
}
